const getViewBoxWidth = (env?: EnvType): number => {
  switch (env) {
    case 'bravo':
    case 'alpha':
    case 'development':
      return 161;
    case 'test':
      return 149;
    case 'production':
    case 'final':
    default:
      return 122;
  }
};

